@media all and (max-width: 1024px) {


#home-hero {
		height: 600px;
	.headline {
		padding: rem-calc(50 25);
		left: 0;
		br {
			display: none;
		}
	}
}
.section .centered h1 {
    font-size: 24px;
}
#schools-partners, #details {
	.col-holder {
		.quarter {
			width: 50%;
			padding: 0 rem-calc(15 20);
			.stats {
				padding: rem-calc(10 0 0);
				display: flex; 
				.number {
					font-size: 50px;
					span {
						top: -15px;
					}
				}
				.text {
					span {
						font-size: 24px;
					}
				}
			}
		}
	}
}
 #details {
 	.col-holder {
		display: flex;
		flex-wrap: wrap;
		.quarter {
			width: 50%;
			padding: rem-calc(20);
			&:first-child, &:nth-child(3) {
				border:none;
			}
			&:nth-child(3), &:nth-child(4) {
				border-top: 1px solid $light-gray;
			}
			img {
				max-height: 80px;
			}
		}
	}
 }
#about {
	.left {
		float: none;
		width: 100%;
	}
	.right {
		float: none;
		width: 100%;
	}
}
#partner {
	.col-holder {
		padding: 0;
	}
}
#social-feed {
	.centered {
		h3 {
			font-size: 20px;
		}
		.kebo-tweets.list {
			.ktweet {
				width: calc(48% - 2px);
			}
		}
		
	}
}
.page-template-about {
	font-size: 14px;
	.section:not(#about-hero){
		
		h2 {
			
			font-size: 22px;
		}
		.support {
			h3 {
				font-size: rem-calc(18);
			}
			ul {
				li {
					font-size: 14px;
				}
			}
		}
	&#more-info {
		.col {
			width: 32%;
		}
	}
	}
}
.single-school, .single-partnership-school {
	
	#tasks h2 {
		margin: 0;
		padding-bottom: 25px;
		border-bottom: 1px solid $light-gray;
		&:before {
			display: none;
		}
	}
	.secondary {
		.left .index ul {
	    	margin: 0;
	    	li {
	    		font-size: 12px;
	    	}
		}
		.right:before {
		    top: 0;
		    height: 100%;
		}
	}
}
.page-template-skills-fellows, .page-template-project-leads {
	#fellows-hero p {
		font-size: 14px;
	}

	#fellows-list {
		.grid-container {
			display: block;
 			.column.left { 
 				width: 100%;
 				padding:0;
 				.bio-post { 
 					display: block !important;
	 				border-right: none;
	 				&:before {
	 					display:none !important;
	 				}
	 				&:nth-child(3n) {
						.additional-content {
							> h4 {
								color: $blue;
							}
							.building_block_spotlight_quote {
								background: $blue;
							}
						}
					}	
					&:nth-child(3n + 1) {
						.additional-content {
							> h4 {
								color: $green;
							}
							.building_block_spotlight_quote {
								background: $green;
							}
						}
					}	
					&:nth-child(3n + 2) {
						.additional-content {
							> h4 {
								color: $orange;
							}
							.building_block_spotlight_quote {
								background: $orange;
							}
						}
					}	
				}
 			}
 			.column.right {
 				display: none;
 			}

 		}
 	}
 }
 .single-skills_fellows, .single-project_lead {
	#fellows-hero p {
		font-size: 14px;
	}
  	#fellows-list {
		.grid-container {
			.bio-post {
				display: block;
				padding: rem-calc(0 20);
				.top {
					width: 100%;
					border: none;
					padding: 0;
				}
				.additional-content {
					width: 100%;
					padding: rem-calc(35 0 0);
				}
			}
		}
	}
	
 }

 .page-template-integrated-partnership-schools {
	.hero p {
		font-size: 14px;
	}
  	#schools-list {
		.grid-container {
			.school-post {
				display: block;
				width: 100%;
				padding: rem-calc(50 10);
				border-right: none !important;
				border-top: 1px solid $line-gray !important;
				.top {
					text-align: center;
					padding: rem-calc(0 0 30);
					h3 {
						width: 45%;
					}
					.partner-logo{
						width: 40%;
						text-align: left;
						padding-left: 20px;
					}
					.plus {
						width: 10%;
					}
				}
			&:first-child {
				border-top: none !important;
				padding-top:0;
			}
			}
		}
	}	
 }

/* end max-width 1024 */
}



@media all and (max-width: 768px) {

.infographic {
	h3 {
		font-size: 22px;
	}
	.top {
		.third {
			width: 50%;
			padding: rem-calc(0 10 0 0);
			p {
				font-size: 14px;
			}
			&.middle {
				display: none;
			}
		}
	}
	.green-bubble {
		p {
			font-size: 13px;
		}
	}
	.bottom {
		padding-top: rem-calc(20);
		.mobile-only {
			display: block;
		}
		.half {
			width: 50%;
			float: left;
			padding: 0 rem-calc(20) 0 0;
			.green-bubble {
				&:after {
				    right: 20px;
				}

			}

			&.right {
				padding: 0 0 0	rem-calc(20);
				.green-bubble {
					&:after {
					    left: 20px;
					}
				}
			}
		}
	}
}
#partner {
	.col-holder {
		padding: 0;
		.quarter {
			width: 100%;
			float:none;
			text-align: left;
			padding: rem-calc(0 0 30);
			.logo-image {
				width: 49%;
				max-width: 100%;
				height: auto;
				display: inline-block;
				margin:0;
				padding-right: 20px;
				text-align: right;
				img {
					max-width: 120px;
					max-height: 100px;
				}
			}
			h6 {
				font-size: rem-calc(16);
				display: inline-block;
				vertical-align: middle;
				width: 49%;
				padding-top: 5px;
			}
		}

	}
}
#about {
	.left {
		padding-right: 0;
	}
	.right {
		padding-right: 25px;
	}
}
.page-template-about {
	.section:not(#about-hero){
		h2 {
			font-size: 22px;
		}
		.support {
			h3 {
				font-size: rem-calc(18);
			}
			
		}
	
	&#schools-partners {
		.list {
			ul {
				li {
					ul {
						width: 100%;
						float: none;
						margin: rem-calc(15 0 0);
						li a {
							font-size: 13px;
						}
						
					}
				}
			}
		}
	}
	
	&#skills-framework {
		.blocks {
			h4.lined {
				font-size: 16px;
			}
			.block-holder {
				width: 50%;				
				.block-label {
					line-height: 1.2em;
					padding: rem-calc(25 0 0 5);
				}		
			}
		}
	}
	&#performance-tasks {
		
		.narrow {
			font-size: 16px;
			ul {
				li {
					padding: rem-calc(10 0 0 30);
					position: relative;
					line-height: 1.2em;
					font-size: 13px;
					min-height: 35px;
					&:before {
						display: block;
						position: absolute;
						left: 0;
					}
				}
				
			}
		}
		.types {
			p.icon-above {
				font-size: 16px;
				
			}
			ul {
				list-style: none;
				margin: 0;
				li {
					display: block;
					width: 100%;
					padding: rem-calc(0 0 30);
					strong {
						display: block;
						text-transform: uppercase;
						font-weight: 700;
						font-size: 14px;
						letter-spacing: 0.04em;
					}
				}
			}
		}

	}

	&#innovations {
		.top {
			font-size: 16px;
		}
		.types {
			p.icon-above {
				font-size: 16px;
			}
			.type-holder {
				.half {
					width: 100%;
					display: block;
					padding: rem-calc(0 0 40);
					.text {
						padding-left: rem-calc(10px);
						h6 {
							font-size: rem-calc(14);
						}
					}
				}
			}
		}
	}
	&#products {
		.narrow {
			ul {
				width: 100%;
				display: block;
			}
		}
	}

	&#more-info {
		.col {
			width: 100%;
		&:first-child {
			padding-bottom: 20px;
		}
		}
	}


	}

}


.page-template-performance-tasks {
	#pt-list {
		padding: rem-calc(0 0 60);
		.left,.right {
			width: 100%;
			padding: rem-calc(0);
			float: none;
		}
		.left .cat-container:nth-child(2),
		.right .cat-container:nth-child(1) {
			h2 {
				span {
					color: $blue;
					position: relative;
					&:before {
						content:'2';
						color: $white;
						position: absolute;
						display: block;
						width: 100%;
						text-align: center;
					}

				}
			}	
		}
		.right .cat-container:nth-child(1){
			h2 {
				span {
					&:before {
						content:'3';
					}
				}
			}	
		}
		
	}
}
.page-template-partners {
	#partners-hero {
		background: $orange;
		.centered {
			max-width: 850px;
			margin: auto;
			padding: rem-calc(50 0);
		}
		h1 {
			color: #fff;
		}
		p {
			font-size: rem-calc(16);
			line-height: 1.5;
			color: #fff;
			letter-spacing: 0.04em;
			margin: rem-calc(15 0 0);
			strong {
				text-transform: uppercase;

			}
		}
	}
	#parter-list {
		.cat-container {
			.left {
				float: none;
				width: 100%;
				padding: rem-calc(0 0 25 0);
			}
			.right {
				float: none;
				width: 100%;
				border-left: none;
				padding: rem-calc(0);
			}
		}
	}
}


/* end max-width 768 */
}


@media all and (max-width: 767px) {

	.single-school, .single-partnership-school {
		.hero {
			.left {
				float: none;
				width: 100%;
				padding: rem-calc(0 0 20);
			}
			.right {
				padding: rem-calc(0);
				float: none;
				width: 100%;
				p {
					font-size: 13px;
				}
			}
		}
		h2 {
			font-size: rem-calc(23);
			letter-spacing: 0.03em;
			text-align: left;
			span {
				padding: 0 5px 0 60px;
				&:before {
					font: 30px/50px FontAwesome;
					width: 50px;
					height: 50px;
				}

			}
		}
		#tasks h2 {
			border:none;
		}
		.secondary {
			.left {
				border: none;
				float: none;
				width: 100%;
				.index {
					margin-left: -20px;
					margin-right: -20px;
					border-top: 1px solid $light-gray;
					ul {
						margin: 0px 0 0;
						li {
						padding: rem-calc(17 43 15 50);
						position: relative;
						background: #fff;
						&:before {
							left: 20px;
							font-size: 15px;
							width: 22px;
							height: 22px;
							line-height: 20px;
							margin: -10px 0 0 0;
						}						
						&:after {
							content: "\f055";
							font: 18px/1em FontAwesome;
							position: absolute;
							top: 50%;
							margin-top: -9px;
							right: 20px;
							color: $lightish-gray;
							@include transition(all 0.3s ease);
						}
						&.active {
							background: $white-gray;
							color: #bbb;
							width: 100%;
							&:after{
								visibility: hidden;
							}
						}

						}
					}
				}
				.skills {
					display: none;
				}
			}
			.right {
				text-align: left;
				float: none;
				width: 100%;
				padding: rem-calc(30 0 80 0);
				&:before {	
					display:none;
				}
				.task-container {
					h6 {
						font-size: 15px;
						span {
							font-size: 16px;
						}
					}
					p, li {
						font-size: 13px;
					}
				}
			}
			&#innovation .right h3 {
				font-size: 16px;
			}
		}
	
	}

	.single-partnership-school h2 span{
	    padding: rem-calc(0 0 0 60);
	    display: inline-block;
	}
/* end max-width 767 */
}




@media all and (max-width: 520px) {

#schools-partners {
	.col-holder {
		display: block;
		.quarter {
			width: 100%;
			padding: rem-calc(0 0 20);
		}
	}
}
#details {
 	.col-holder {
		display: block;
		.quarter {
			width: 100%;
			padding: rem-calc(20 0);
			border-left: none;
			border-top: 1px solid $light-gray;
			
			&:first-child {
				border:none;
				padding-top: 0;
			
			}
			img {
				max-height: 80px;
			}
		}
	}
	
}

/* end max-width 520 */
}



@media all and (max-width: 480px) {

.infographic {
	h3 {
		font-size: 18px;
		margin-bottom: 10px;
	}
	.top {
		.third {
			width: 100%;
			padding: rem-calc(0 0 15 0);
		}
	}
	.green-bubble {
		padding: rem-calc(20 20 25);
	}
	.bottom {
		padding-top: rem-calc(20);
		.mobile-only {
			display: block;
		}
		.half {
			width: 50%;
			padding: 0 rem-calc(5) 0 0;
			.green-bubble {
				&:after {
				    right: 50px;
				}

			}
			&.right {
				padding: 0 0 0	rem-calc(5);
				.green-bubble {
					&:after {
					    left: 50px;
					}
				}
			}
		}
	}
}
#goals {
	ul {
		max-width: rem-calc(800);
		margin: auto;
		list-style: none;
		li {
			width: 100%;
			text-align: left;
			display: block;
			vertical-align: top;
			padding: rem-calc(0 15 25 45);
			position: relative;
			font-size: rem-calc(14);
			line-height: 1.5em;
			font-weight: 600;
			&:before {
				content:"\f00c";
				font: normal normal normal 20px/30px FontAwesome;
				display: block;
				position: absolute;
				left:0;
				top:0px;
				width: rem-calc(30);
				height: rem-calc(30);
				background: $green;
				text-align: center;
				border-radius: 50%;
			}
		}
	}
}

#social-feed {
	.centered {
		h3 {
			font-size: 18px;
			.tweet-icon {
				display: block;
				margin: 0 auto rem-calc(20px);
				
			}
		}
		.kebo-tweets.list {
			.ktweet {
				width: 98%;
				margin-bottom: 10px;
			}
		}
		
	}
}
.page-template-about {
	font-size: 13px;
	.section:not(#about-hero){
		h2 {
			font-size: 20px;
		}
		.support {
			h3 {
				font-size: rem-calc(16);
			}
			
		}
	
	
	
	&#skills-framework {
		.blocks {
			h4.lined {
				font-size: 15px;
			}
			.block-holder {
				width: 100%;				

			}
		}
	}

	&#innovations {
		.top {
			font-size: 16px;
		}
		.types {
			p.icon-above {
				font-size: 16px;
			}
			.type-holder {
				.half {
					width: 100%;
					display: block;
					padding: rem-calc(0 0 40);
					.img {
						float:left; 
						width: 18%;
						text-align: center;
						svg {
							max-width: 90%;
							max-height: 60px;
							opacity: 0.75;
						}
					}
					.text {
						float:left;
						width: 82%;				
						padding-left: rem-calc(8px);
						h6 {
							font-size: rem-calc(13);
						}
					}
				}
			}
		}
	}

	}
}

.page-template-performance-tasks #pt-list .cat-container .post-list .post .show-hide .bottom .sl-button {
	float:none;
}
.page-template-partners {

	#parter-list {
		.cat-container {
			.right {
				.post {
					.logo {
						width: 70px;
						padding: 0;
					}
					.description {
						width: calc(100% - 70px);
						padding: rem-calc(0 0 0 12);
						p {
							font-size: 13px;
						}
						.title {
							font-size: 14px;
						}
					}

				}
			}
		}

	}
}
.single-school,.single-partnership-school {
	.secondary  .right .task-container {
		.question {
			margin: rem-calc(20 12 30 0);
			&:after {
				width: 37px;
				height: 16px;
				background-position:  0 -287px;
				background-size: 125px auto;
			}
		}
		&:nth-child(2){
			.question:after {
				background-position: 0px -312px;
			}
		}
		&:nth-child(3){
			.question:after {
				background-position: 0px -337px;
			}		
		}

	}
} 
.page-template-project-leads,.page-template-skills-fellows, .single-skills_fellows, .single-project_lead  {
	#fellows-list .grid-container {
		.bio-post {
			padding-left: 0;
			padding-right: 0;
			 .bio-pic {
				display: block;
				margin: auto;
				padding:0;
				img {
					margin: 0;
				}
			}
			.description {
			    display: block;
			    width: 100%;
			    text-align: center;
			    h3:after {
			    	margin: 10px auto;
			    }
			    p {
			    	text-align: left;
			    	margin-top: rem-calc(15);
			    }
			}
			.additional-content {
				padding: 0;
			}
		}	
	}
}
.single-skills_fellows, .single-project_lead  {
	#fellows-list .grid-container {
		.bio-post {
			.top {
				.current-role p {
					margin: rem-calc(0);
				}
			}
			.additional-content {
				padding: rem-calc(25 0 0);
			}

		}
	}
}
 .page-template-integrated-partnership-schools {
  	#schools-list {
		.grid-container {
			.school-post {
				.top {
					h3 {
						width: 45%;
						font-size: 22px;
					}
					.partner-logo{
						width: 40%;
						text-align: left;
						padding-left: 10px;
						img {
							max-width: 90%;
						}
					}
					.plus {
						width: 10%;
					}
				}
			}
		}
	}	
 }

/* end max-width 480 */
}